<i18n>
{
  "en": {
    "TakePortableBattery": "Take portable battery",
    "successfully": "Portable battery has been successfully ejected, please take it away in time.",
    "experience": "Begin to experience"
  },
  "zh": {
    "TakePortableBattery": "取充电宝",
    "successfully": "充电宝已成功弹出，请及时取走",
    "experience": "开始体验"
  }
}
</i18n>

<template>
  <div class="takeSuccessfully">
    <head-top :title='$t("TakePortableBattery")' :custom-back="true" @goBackHander="goBackHander"></head-top>
    <div class="container">
      <img src="../../../assets/scanning_images/succeed.png" alt="">
      <span>{{ $t("successfully") }}</span>
      <button @click="goHandle" class="btn">{{ $t("experience") }}</button>
    </div>
  </div>
</template>

<script>
import headTop from '../../../components/Header/index'
import { routerGo } from '../../../utils/router.js'
export default {
  name: 'takeSuccessfully',
  data () {
    return {}
  },

  components: {
    headTop
  },

  methods: {
    goBackHander () {
      routerGo(-4)
    },
    goHandle () {
      this.dataInteractionRequest({
        msgType: '0x5221',
        type: 3,
        page: 1,
        count: 10
      })
      routerGo(-4)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.takeSuccessfully{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    img{
      height: 185px;
      margin: 110px auto 70px auto;
    }
    span{
      font-size: 30px;
      display: block;
      padding: 0 6%;
    }
    button{
      .wh(690px, 100px);
      background: @themeColor;
      border-radius: 10px;
      font-size: 30px;
      position: absolute;
      left: 50%;
      bottom: 50px;
      margin-left: -345px;
      // box-shadow: 0px 5px 10px rgba(244, 218, 33, 0.6)
      box-shadow: 0px 4px 25px @themeColor;
    }
  }
}
</style>



